.servicesContainer{
    padding-bottom: 10vmin;
}

.serviceTitle{
    display: flex;
    justify-content: center;
}

.serviceTitle > h1{
    color:white;
    font-size: 50px;
}

.servicesTitles{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.servicesTitles > p{
    color: white;
}

.servicesContainer{
    margin-top: 20vmin;
    padding-top: 5vmin;
    padding-left: 5%;
    padding-right: 5%;
}

.serviceColumns{
    display: flex;
    flex-direction: row;
}

.descriptionColumn {
    display: flex;
    flex-direction: column;
    width: 50%;
    align-items: center;
}

.descriptionElement {
    display: flex;
    flex-direction: row;
    width: 70%;
    margin-bottom: 6vmin;
    background: rgba(100, 206, 132, 0.4);
    border: 1px solid rgba( 100, 206, 132, 0.18 );
    backdrop-filter: "blur(10px)";
    border-radius: 15px;
    cursor: pointer;
}

.logo {
    height: 6vmin;
    width: 6vmin;
    border-radius: 100%;
    margin-left:3vmin;
    margin-top: 3vmin;
}

.description{
    padding-left: 5vmin;
}

.logoColumn {
    width: 50%;
}

.container{
    display: grid;
    overflow: hidden;
    margin: 0;
    list-style: none;
    grid-template-columns: repeat(2,1fr);
    gap: 10vmin;
    padding:5vmin
}

.skillLogo{
    height: 8vmin;
    width: 8vmin;
    justify-self: center;
}
