.sponsorsContainer {
    padding-top:8vmin;
    padding-bottom:18vmin;
}

.sponsor{
    margin: auto;
    height: 8vmin;
    width: 8vmin;
    margin-right: 10vmin;
    margin-left: 10vmin;
}

/* Animation */
@keyframes scroll {
	0% { transform: translateX(0); }
	100% { transform: translateX(calc(-250px * 7))}
}


/*  Styling  */
#logos-home-page .slider {
    height: auto;
    margin: auto;
    overflow: hidden;
    position: relative;
    width: 100%;
}



	#logos-home-page .slide-track {
		animation: scroll 40s linear infinite;
		display: flex;
        width: calc(250px * 14);
	}
    #logos-home-page div.slide {
        padding: 0 12px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
	
